// Header.jsx
import React from "react";
import "./headerStyle.sass";
import { useSelector } from "react-redux";

export function Header({ chatName }) {
  const request = useSelector(({ chatReducer }) => chatReducer.request);
  let chatStatus;
  if (request.status == "IDLE") chatStatus = "";
  if (request.status == "LOADING") chatStatus = " ";
  if (request.status == "SUCCESS") chatStatus = " ";
  if (request.status == "FAILURE") chatStatus = "  ";
  return (
    <div className="headerDiv">
      <span className="headerSpan">
         {chatName} {chatStatus}
      </span>
    </div>
  );
}
